  function scrollMap() {
    var scrollDistance = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
    if (scrollDistance > 1600 && !$('.map .city-list li').hasClass('anim')) {
      $('.map .city-list li').addClass('anim');
      // numScroll(1, 15, $('.num-year'));
      // numScroll(1, 43, $('.num-brand'));
